.fnf {
    height: 100%;
    background-color: #f0b6f6;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;
}
 
 
.fnf-description {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin-left: 2rem;
    padding: 2rem;
}
 
.fnf-description > h1{
    font-size: 7rem;
    margin-bottom: 20px;
    font-family: 'Roboto Condensed', sans-serif;
    -webkit-animation: heading 3s infinite;
            animation: heading 3s infinite; 
    color: #ac3b73;
}
 
.fnf-description > h2 {
    font-size: 3rem;
    font-family: 'Source Sans Pro', sans-serif;
    text-align: center;
    color: #ac3b73;
}
 
 
 
.fnf-description > button, .fnf-description > button:hover {
    margin-top: 2rem;
    font-size: 18px;
    padding: 0.7rem 2.1rem;
   text-transform: inherit;
   color: #fff;
   background-color: #ac3b73;
   border: 1px solid;
   box-sizing: border-box;
   background-size: 150% 150%;
   background-position-x: 0%;
   border-radius: 70px;
   font-family: Nunito;
   width: 7rem;
}
 
