#fof_star1{
    -webkit-transform-origin:86% 87%;
            transform-origin:86% 87%;
    -webkit-animation: starSpin1 3s infinite both ease-in-out alternate;
            animation: starSpin1 3s infinite both ease-in-out alternate;
}

#fof_star2{
    -webkit-transform-origin: 92% 36%;
            transform-origin: 92% 36%;
    animation: starSpin2 5s infinite 1.5s both ease alternate-reverse;
}
#fof_star3{
    -webkit-transform-origin:7% 21%;
            transform-origin:7% 21%;
    animation: starSpin1 3s infinite 1s both ease alternate-reverse;
}

#fof_star4{
    -webkit-transform-origin: 47.5% 15%;
            transform-origin: 47.5% 15%;
    -webkit-animation: starSpin2 5s infinite 0.5s both ease alternate;
            animation: starSpin2 5s infinite 0.5s both ease alternate;
}

@-webkit-keyframes starSpin1{
    from{
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
    }
    to{
        -webkit-transform: scale(1.2) ;
                transform: scale(1.2) ;
    }
}

@keyframes starSpin1{
    from{
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
    }
    to{
        -webkit-transform: scale(1.2) ;
                transform: scale(1.2) ;
    }
}

@-webkit-keyframes starSpin2{
    from{
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
    to{
        -webkit-transform: scale(1.3) rotate(360deg);
                transform: scale(1.3) rotate(360deg);
    }
}

@keyframes starSpin2{
    from{
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
    to{
        -webkit-transform: scale(1.3) rotate(360deg);
                transform: scale(1.3) rotate(360deg);
    }
}

#fof_astronaut{
    -webkit-transform-origin: 25% 82%;
            transform-origin: 25% 82%;
    -webkit-animation: muskWave 2.5s infinite both ease-in-out alternate;
            animation: muskWave 2.5s infinite both ease-in-out alternate;
}
@-webkit-keyframes muskWave{
    from{
