@-webkit-keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-20px);
		        transform: translatey(-20px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}
 
 
 
@keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-20px);
		        transform: translatey(-20px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}
 
 
 
@-webkit-keyframes heading {
    0% {
       -webkit-animation-timing-function: cubic-bezier(.22,.88,.44,1);
               animation-timing-function: cubic-bezier(.22,.88,.44,1);
       opacity: 0.2;
       -webkit-transform: translateY(100vh) scale(0.8,0.8) rotateZ(-20deg) rotateX(10deg);
               transform: translateY(100vh) scale(0.8,0.8) rotateZ(-20deg) rotateX(10deg);
    }
 
    30%,
    50% {
       -webkit-animation-timing-function: cubic-bezier(.88,0,1,.44);
               animation-timing-function: cubic-bezier(.88,0,1,.44);
       opacity: 1;
       -webkit-transform: translateY(0%) scale(1,1);
               transform: translateY(0%) scale(1,1);
    }
 
    100% {
       -webkit-transform: translateY(-100vh) rotateZ(20deg) rotateX(10deg);
               transform: translateY(-100vh) rotateZ(20deg) rotateX(10deg);
    }
 }
 
 
 
@keyframes heading {
    0% {
       -webkit-animation-timing-function: cubic-bezier(.22,.88,.44,1);
               animation-timing-function: cubic-bezier(.22,.88,.44,1);
       opacity: 0.2;
       -webkit-transform: translateY(100vh) scale(0.8,0.8) rotateZ(-20deg) rotateX(10deg);
               transform: translateY(100vh) scale(0.8,0.8) rotateZ(-20deg) rotateX(10deg);
    }
 
    30%,
    50% {
       -webkit-animation-timing-function: cubic-bezier(.88,0,1,.44);
               animation-timing-function: cubic-bezier(.88,0,1,.44);
       opacity: 1;
       -webkit-transform: translateY(0%) scale(1,1);
               transform: translateY(0%) scale(1,1);
    }
 
    100% {
       -webkit-transform: translateY(-100vh) rotateZ(20deg) rotateX(10deg);
               transform: translateY(-100vh) rotateZ(20deg) rotateX(10deg);
    }
 }
 

